interface GovernmentScheme {
  id: string
  name: string
  description: string
  eligibility: string[]
  benefits: string
  applicationProcess: string
  documents: string[]
  deadline?: string
  category: string
  status: 'active' | 'upcoming' | 'closed'
}

interface WeatherData {
  location: string
  temperature: number
  condition: string
  humidity: number
  windSpeed: number
  forecast: {
    day: string
    high: number
    low: number
    condition: string
  }[]
  alerts: string[]
}

